.button {
  display: block;
  /* align-items: center; */
  text-align: center;
  margin: 0 auto;
  margin-bottom: 25px;
  min-width: 200px;
  padding: 5px 30px;
  box-shadow: rgb(0 0 0 / 15%) 0px 4px 4px;
  border-radius: 4px;
  border-style: none;
  cursor: pointer;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.88;
  letter-spacing: 0.06em;
}

.button:hover,
.button:focus {
  color: rgb(255, 255, 255);
  background-color: rgb(33, 150, 243);
}
